import React from 'react';
import cls from './Switcher.module.css';
import {NavLink} from "react-router-dom";

const Switcher = (props) => {
    return (
        <div className={cls.btnGroup}>
            <div className="btn-group" role="group" aria-label="Basic example">
                <NavLink className={cls.navLink} activeClassName={cls.active} to={props.path.food}>
                    <p className={cls.top}><i className="fa fa-cutlery"/>Пицца</p>
                </NavLink>
                <NavLink className={cls.navLink} activeClassName={cls.active}  to={props.path.drinks}>
                    <p><i className="fa fa-glass"/>Напитки</p>
                </NavLink>
                <NavLink className={cls.navLink} activeClassName={cls.active}  to={props.path.dishes}>
                    <p><i className="fa fa-gift"/>Подарки</p>
                </NavLink>
            </div>
        </div>
    );
};

export default Switcher;
